import React from "react";
import {Card, Row, Col} from "antd";
import axios from "axios";
import {DATA_DISPLAY_URL} from "../../components/api";
import LoadingIndicator from "../loadingIndicator";

class GalleryIM extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      uniqueId: props.uniqueId,
      loadingDisplay: 'block'
    };

    var self = this;

    self.setState({
      images: []
    });

    var imag = [];

    axios.get(DATA_DISPLAY_URL+"?project_id="+self.state.uniqueId, {
    data: 'data'
    })
    .then(function (response) {
      console.log(response);
       if(response.status===200){
         console.log(response);
         let images_ = response.data;
         imag = images_.map(image => {
                    return (
                      <Col sm={6}>
                      <Card style={{backgroundColor: '#11111122'}} className="gx-card" title={image.caption}>
                       <img style={{maxHeight:'200px'}} src={image.src} alt={image.caption} />
                       </Card>
                       </Col>
                  );
                 });
        self.setState({
          images: imag,
          loadingDisplay: 'none'
        });
       }
    })
    .catch(function (error) {
      console.log(error);
    });
  }


  render() {
    return (
      <div>
      <h1>Collection Preview</h1>
      <br/>
      <LoadingIndicator styleProp={this.state.loadingDisplay}/>
      <Row>
      {this.state.images}
      </Row>
      </div>
    );
  }

}

export default GalleryIM;
